import { request } from 'utils';
import { CLIENT_RENEG_LIMIT } from 'tls';

request.prefix = '/api/categories';

export const getListStudyField = (page = 1, IsPagingEnabled, hasSFId) =>
  request({
    url: '/StudyField',
    params: {
      page,
      IsPagingEnabled,
      hasSFId,
    },
    method: 'GET',
  });
export const getStudyField = id =>
  request({
    url: `/StudyField/${id}`,
    method: 'GET',
  });
export const deleteStudyField = id =>
  request({
    url: `/StudyField/${id}`,
    method: 'DELETE',
  });
export const updateStudyField = (id, values) => {
  console.log('values lllllll>', values);
  return request({
    url: `/StudyField/${id}`,
    data: {
      id: values.id,
      label: values.code,
      sfId: values.sfId,
      labelInEnglish: values.labelInEnglish || '',
      trainTypeId: values.trainTypeId || '',
    },
    method: 'PUT',
  });
};
export const createStudyField = values =>
  request({
    url: '/StudyField',
    data: {
      id: values.id,
      label: values.code,
      sfId: values.sfId || null,
      labelInEnglish: values.labelInEnglish || '',
      trainTypeId: values.trainTypeId || '02',
    },
    method: 'POST',
  });



